body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  color: #000000;
  background-color: #FFFFFF;
  font-size: 1.125rem;
  line-height: 1.32rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

main {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.intro {
  margin: 0;
  padding: 0;
}

@media (min-width: 1024px) {
  main {
    width: 100%;
  }
}
@media (min-width: 1440px) {
  main {
    width: 100%;
  }
}